@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
.bubble{position:fixed;top:50%;left:50%;-webkit-transform:translate3d(-50%, -50%, 0);transform:translate3d(-50%, -50%, 0);border-radius:50%;display:flex;width:500px;height:500px;align-items:center;justify-content:center;transition:opacity 0.5s ease, -webkit-transform 3333ms ease;transition:transform 3333ms ease, opacity 0.5s ease;transition:transform 3333ms ease, opacity 0.5s ease, -webkit-transform 3333ms ease}.bubble--inactive{opacity:0.3}.bubble--expanding{-webkit-transform:translate3d(-50%, -50%, 0) scale(1.3);transform:translate3d(-50%, -50%, 0) scale(1.3)}.bubble--shrinking{-webkit-transform:translate3d(-50%, -50%, 0) scale(0.7);transform:translate3d(-50%, -50%, 0) scale(0.7)}.bubble--static{-webkit-transform:translate3d(-50%, -50%, 0) scale(1);transform:translate3d(-50%, -50%, 0) scale(1)}.bubble__box{-webkit-animation:rotation 10s infinite linear;animation:rotation 10s infinite linear;-webkit-animation-delay:1000ms;animation-delay:1000ms;display:block;left:50%;top:0;bottom:0;position:absolute;z-index:1}.bubble__outer{top:0;right:0;bottom:0;left:0;border:5px solid #3278e0;border-radius:50%;position:absolute;display:flex;align-items:center;justify-content:center;overflow:hidden}.bubble__point{width:20px;height:20px;border-radius:50%;display:block;background:#fff;position:absolute}.bubble__point--current{background:#3378da;top:-10px;left:-10px}.bubble__point--top{left:50%;top:-10px;margin-left:-10px}.bubble__point--right{margin-top:-10px;top:75%;right:23px}.bubble__point--left{margin-top:-10px;top:75%;left:23px}.bubble__inner{width:450px;height:450px;border-radius:50%;background:linear-gradient(#82a7de, #7753e7);display:flex;align-items:center;justify-content:center;font-size:32px}@-webkit-keyframes rotation{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotation{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

html{background:#36303e;font-family:'Open Sans', sans-serif;font-size:16px;font-weight:300;color:#fff}

